$main_black: #1D1D1B;
$main_yellow: #FFB21B;
$btn-active: #ED9C0C;
$bg_white: #f8f8f8;

*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Montserrat", sans-serif;
    text-decoration: none;
    border-radius: 0px;
    transition: all .25s ease-in-out 0s;
    color: $main_black; }

html, body {
    overflow-x: hidden;
    background-color: $bg_white; }

body {
    position: relative;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    &.block-scroll {
        overflow: hidden !important;
        width: 100%;
        height: 100%;
        min-height: auto;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10000; } }

.body {
    &_white {
        background-color: #fff; } }

.section {
    &__title {
        font-size: 40px;
        font-weight: 800;
        text-transform: uppercase;
        line-height: 140%;
        &_big {
            font-size: 48px;
            line-height: 58px; }
        &_small {
            font-size: 32px;
            line-height: 51px; }
        &_dib {
            display: inline-block; } }
    &__top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        &_col {
            flex-direction: column;
            align-items: flex-start; } }
    &__more {
        display: inline-block;
        position: relative;
        padding: 5px 0px;
        margin-top: 13px;
        svg {
            margin-left: 10px;
            fill: $main_black;
            transform: rotate(180deg);
            width: 32px;
            height: auto;
            transition-duration: 0s; }
        span {
            position: relative;
            font-weight: 700;
            font-size: 18px;
            line-height: 18px;
            display: inline-block;
            &::after {
                content: '';
                display: inline-block;
                left: 0;
                bottom: -6px;
                position: absolute;
                width: 100%;
                height: 2px;
                background-color: $main_yellow; } }
        &:hover {
            span::after {
                background-color: $main_black; }
            svg {
                fill: $main_yellow; } } }
    &__button {
        &-wrapper {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 70px; } } }

.text {
    &_white {
        color: #ffffff; }
    &_yellow {
        color: $main_yellow; }
    &_bold {
        font-weight: 700 !important; }
    &_semi-bold {
        font-weight: 600 !important; }
    &_regular {
        font-weight: 400 !important; }
    &_tac {
        text-align: center; }
    &_ttu {
        text-transform: uppercase; } }

.button {
    display: inline-block;
    background-color: transparent;
    border-radius: 4px;
    border: none;
    appearance: none;
    position: relative;
    cursor: pointer;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    user-select: none;
    color: $main_black;
    padding: 18px;
    font-size: 20px;
    line-height: 24px;
    font-weight: 800;
    overflow-anchor: none;
    &_request {
        padding: 5px 5px 5px 34px;
        font-size: 14px;
        line-height: 17px;
        font-weight: 700;
        flex-shrink: 0;
        &::before {
            content: '';
            display: inline-block;
            position: absolute;
            left: 0;
            top: 50%;
            height: 22px;
            width: 22px;
            background-repeat: no-repeat;
            background-position: center;
            background-size: cover;
            background-image: url(../img/request-ico.svg);
            transform: translateY(-50%); }
        &:hover {
            color: $main_yellow; } }
    &_transparent {
        width: 100%;
        max-width: 385px;
        border: 2px solid $main_yellow;
        &:hover {
            background-color: $main_yellow;
            color: #fff; }
        &:active {
            background-color: $btn-active;
            border: 2px solid $btn-active; } }
    &_about-page {
        width: 100%;
        max-width: 292px;
        background-color: $main_yellow;
        margin-top: 50px;
        svg {
            circle {
                fill: none;
                stroke: $main_black; }
            path {
                fill: $main_black; }
            vertical-align: middle;
            margin-right: 5px; }
        &:hover {
            color: #fff;
            svg {
                path:not(:last-child) {
                    fill: #fff;
                    stroke: #fff; }
                circle {
                    fill: none;
                    stroke: #fff; } } }
        &:active {
            background-color: $btn-active; } }
    &:focus {
        outline: none; } }

.container {
    max-width: 1290px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    padding: 0px 15px;
    z-index: 3; }

.header, .footer {
    flex-grow: 0; }

.main {
    flex-grow: 1;
    overflow: hidden;
    &_nomt {
        margin-top: 0 !important;
        padding-top: 0 !important; }
    &:not(.main_index) {
        margin-top: 100px;
        padding-top: 40px; } }

.header {
    position: fixed;
    left: 0;
    overflow: hidden;
    top: 0;
    width: 100%;
    z-index: 5;
    box-shadow: 0 0 16px 1px rgba(0, 0, 0, 0);
    &.fixed {
        background-color: $bg_white;
        box-shadow: 0 0 16px 1px rgba(0, 0, 0, .1);
        .header {
            &__wrap {
                padding: 10px 0px; } } }
    &.menu-opened {
        background: linear-gradient(90deg, rgba(255,255,255,1) 0%, rgba(255,255,255,1) 65%, rgba(255,255,255,0) 66%, rgba(255,255,255,0) 100%);
        .header__wrap {
            padding-top: 20px;
            padding-bottom: 3px; } }
    &__wrap {
        padding-top: 38px;
        padding-bottom: 30px;
        display: flex;
        align-items: center;
        justify-content: space-between; }
    .nav {
        margin-left: 70px; }
    .nav, .button_request {
        margin-bottom: 15px; } }

.logo {
    display: block;
    max-width: 135px;
    height: auto;
    &__img {
        width: 100%;
        height: auto; } }

.nav {
    width: 100%;
    max-width: 500px;
    &__list {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        list-style-type: none; }
    &__link {
        color: $main_black;
        text-transform: uppercase;
        font-size: 14px;
        display: inline-block;
        padding: 5px;
        position: relative;
        &::after {
            display: inline-block;
            content: '';
            width: calc( 100% - 10px );
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 0);
            height: 2px;
            background-color: $main_yellow;
            position: absolute;
            opacity: 0; }
        &.active {
            &::after {
                opacity: 1; } }
        &:hover {
            color: $main_yellow; } } }

.burger {
    display: none;
    height: 24px;
    width: 24px;
    span, &::before, &::after {
        content: '';
        display: inline-block;
        width: 24px;
        height: 3px;
        background-color: $main_black;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        transition:  top .15s ease-in .15s, transform .15s ease-in 0s, opacity .1s ease-in .15s; }
    &::before {
        top: calc( 9px + 50% ); }
    &::after {
        top: calc( -9px + 50% ); }
    &.active {
        span, &::before, &::after {
            transform: translate(-50%, -50%);
            top: 50%;
            transition:  top .15s ease-in 0s, transform .15s ease-in .15s, opacity .15s ease-in .1s; }
        span {
            opacity: 0; }
        &::before {
            transform: translate(-50%, -50%) rotate(45deg); }
        &::after {
            transform: translate(-50%, -50%) rotate(-45deg); } } }

.menu {
    position: fixed;
    height: 100%;
    width: 100%;
    background-color: #fff;
    top: 0;
    left: 0;
    z-index: 4;
    transform-origin: left top;
    transform: translateX(100%) rotate(-30deg);
    visibility: hidden;
    pointer-events: none;
    padding: 0px 5px;
    overflow: hidden;
    display: none;
    &::before {
        transition-duration: .35s;
        transition-timing-function: ease-out;
        content: '';
        display: block;
        height: 100%;
        position: sticky;
        position: absolute;
        float: right;
        float: none;
        top: 0;
        right: 20px;
        border-radius: 30px;
        background-color: $main_yellow;
        width: 200px;
        transform: translateX(165px) rotate(-9deg);
        transform-origin: left top;
        transition-delay: .1s; }
    &__container {
        // max-width: 335px
        padding: 0px 10px 20px;
        width: calc( 100% - 10px );
        margin: 0 auto 10px;
        height: 90%;
        display: flex;
        flex-direction: column;
        align-items: stretch;
        justify-content: center;
        position: absolute;
        overflow-y: auto;
        top: 0;
        .nav {
            margin-bottom: 30px;
            margin-left: 0px;
            &__list {
                flex-direction: column;
                align-items: flex-start; }
            &__link {
                font-size: 42px;
                line-height: normal;
                padding-left: 0; }
            &__item {
                display: inline-block; } }
        .nav__item + .nav__item {
            margin-top: 40px; }
        .button {
            border-radius: 4px;
            border: 2px solid $main_black;
            font-size: 18px;
            padding: 18px 15px;
            position: relative;
            // bottom: 48px
            margin-top: auto;
            width: 100%;
            &::before {
                position: relative;
                vertical-align: middle;
                transform: translateY(-2px);
                margin-right: 12px; }
            &:hover, &:active {
                border: 2px solid $main_yellow; } } }
    &.active {
        transform: translateX(0%) rotate(0deg);
        visibility: visible;
        pointer-events: all;
        opacity: 1;
        &::before {
            transform: translateX(165px) rotate(4deg); } } }

.slide {
    padding-top: 225px;
    padding-bottom: 70px;
    position: relative;
    &__bg {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
        &:not(.active) {
            visibility: hidden;
            opacity: 0; }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    &__content {
        max-width: 820px; }
    &__images {
        position: absolute;
        bottom: -60px;
        right: 20px;
        z-index: -1;
        width: 100%; }
    &__image {
        position: absolute;
        bottom: 0;
        right: 0;
        &-item {
            position: absolute;
            right: -47px;
            bottom: 0; }
        &-bg {
            right: 0;
            bottom: 0; }
        &-mobile {
            display: none;
            position: absolute;
            height: 100%;
            width: 100%;
            img {
                height: 100%;
                width: 100%;
                object-fit: cover; } }
        &:nth-child(2) &-item {
            right: -40px; }
        &:nth-child(3) &-item {
            right: -45px; }
        &:not(.active) {
            .slide {
                &__image {
                    &-mobile {
                        opacity: 0;
                        visibility: hidden; }
                    &-item {
                        visibility: hidden;
                        transform: translateX(130%);
                        opacity: 0; }
                    &-bg {
                        visibility: hidden;
                        opacity: 0; } } } } }
    &__title {
        &, & * {
            font-size: 102px;
            font-weight: 800;
            text-transform: uppercase;
            line-height: 122px; } }
    &__names {
        position: relative;
        height: 38px;
        margin-top: 15px;
        overflow: hidden; }
    &__game {
        font-size: 24px;
        font-weight: 600;
        line-height: 38px;
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        &:not(.active) {
            transform: translateY(110%); } }
    &__link {
        display: inline-block;
        position: relative;
        text-transform: uppercase;
        font-size: 14px;
        font-weight: 700;
        line-height: 22px;
        margin-top: 5px;
        &:not(.active) {
            display: none; }
        &::after {
            content: '';
            display: inline-block;
            position: absolute;
            width: 100%;
            background-color: $main_yellow;
            height: 2px;
            left: 0;
            bottom: 0; }
        &:hover {
            color: $main_yellow; } }
    &__controls {
        display: flex;
        align-items: center;
        margin-top: 180px; }
    &__control {
        height: 12px;
        width: 34px;
        padding: 10px 0px;
        position: relative;
        svg {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            fill: $main_black;
            width: 100%;
            height: auto; }
        & + & {
            margin-left: 22px;
            svg {
                transform: translate(-50%, -50%) rotate(180deg); } }
        &:hover {
            svg {
                transition-duration: 0s;
                fill: $main_yellow; } } } }

.games {
    padding: 25px 0px 0px;
    &__container {
        display: grid;
        grid-template-columns: 2fr repeat(2, 1fr);
        grid-gap: 30px;
        grid-template-rows: repeat(2, 210px);
        margin-top: 10px; }
    &__img {
        height: 100%;
        width: 100%;
        position: relative;
        position: relative;
        z-index: 3;
        img {
            z-index: 1;
            height: 100%;
            width: 100%;
            object-fit: cover;
            transition-duration: .6s;
            transform: scale(1.03);
            z-index: 1; } }
    &__item {
        overflow: hidden;
        border-radius: 8px;
        position: relative;
        z-index: 3;
        &:hover {
            img {
                transform: scale(1.06); } }
        &:first-child {
            grid-row: 1 / 3; } } }

.about {
    padding-top: 150px;
    padding-bottom: 150px;
    background-image: url(../img/about_bg.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    &__bandito {
        width: 460px;
        height: 470px;
        box-shadow: 0px 14px 54px rgba(0, 0, 0, 0.1);
        border-radius: 20px;
        position: relative;
        z-index: 2;
        flex-shrink: 0;
        &-wrap {
            position: relative;
            &::before {
                content: '';
                display: block;
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                border-radius: 50%;
                background-color: #fff;
                opacity: .5;
                z-index: -2;
                height: 560px;
                width: 560px; } } }
    &__container {
        display: flex;
        align-items: center;
        justify-content: space-between;
        max-width: 1125px;
        width: 100%;
        margin: 0 auto; }
    &__content {
        max-width: 470px; }
    &__text {
        font-size: 18px;
        line-height: 28px;
        & + & {
            margin-top: 1em; } }
    .section__title {
        margin-bottom: 5px; }
    .section__more {
        margin-top: 20px; } }

.bandito {
    overflow: hidden;
    transform: rotate(180deg);
    padding: 30px 10px;
    background-color: #fff;
    &_blocked {
        transform: none;
        background-size: cover;
        background-position: center;
        background-repeat: no-repeat;
        background-image: url(../img/bandito.png);
        padding: 0px;
        background-color: transparent;
        .bandito {
            &__body {
                display: none; } } }
    &__body {
        padding-bottom: 91%;
        height: 100%;
        position: relative;
        overflow: hidden; }
    &__item {
        width: 100%;
        border-radius: 4px;
        background-color: #f6f6f6;
        position: relative;
        overflow: hidden;
        transition-duration: .2s;
        transition-timing-function: ease-out;
        transform: rotate(180deg);
        svg {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            fill: $main_yellow;
            opacity: .5;
            height: 70px;
            width: 70px; }
        & + & {
            margin-top: 28px; }
        &.active {
            background-color: $main_yellow;
            box-shadow: 0px 12px 20px rgba(255, 178, 27, 0.4);
            svg {
                opacity: 1;
                fill: #ffffff; } } }
    &__col {
        position: absolute;
        height: 100%;
        top: 0;
        width: 33.34%;
        padding: 0px 15px;
        transition-duration: 1s;
        &:nth-child(2) {
            margin-left: 33.3333%;
            transition-delay: .5s; }
        &:nth-child(3) {
            margin-left: 66.6666%;
            transition-delay: 1s; } } }

.benefits {
    padding-bottom: 80px;
    &__container {
        display: flex;
        align-items: center; } }

.benefit {
    width: 33.3333333%;
    height: 225px;
    background-color: #f0f0f0;
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0px 20px;
    &__title {
        text-align: center;
        font-weight: 700;
        font-size: 24px;
        position: relative;
        &::after {
            content: '';
            display: block;
            position: absolute;
            bottom: -10px;
            width: 95px;
            height: 2px;
            background-color: $main_yellow;
            left: 50%;
            transform: translate(-50%, 100%); } }
    &:nth-child(2) {
        background-color: transparent;
        background-image: url(../img/benefit_yellow.png);
        background-size: contain;
        background-repeat: no-repeat;
        height: 270px;
        margin: 0 -15px;
        z-index: 2;
        .benefit {
            &__title {
                color: #fff;
                &::after {
                    background-color: #fff; } } } } }

.news {
    padding-bottom: 80px;
    &__container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 35px;
        margin-top: 30px; }
    &__item-img {
        height: 160px;
        width: 100%;
        border-radius: 8px;
        overflow: hidden;
        display: block;
        position: relative;
        z-index: 3;
        img {
            z-index: 1;
            height: 100%;
            width: 100%;
            object-fit: cover;
            transition-duration: .5s; } }
    &__link {
        margin-top: 25px;
        font-weight: 600;
        font-size: 24px; }
    &__item {
        display: block;
        &:hover {
            .news__link {
                color: $main_yellow; }
            .news__item-img img {
                transform: scale(1.03); } } }
    &__date {
        font-size: 12px;
        font-weight: 700;
        margin-top: 10px;
        display: inline-block; }
    &__preview {
        font-size: 16px;
        line-height: 160%;
        display: inline-block;
        margin-top: 10px; } }

.footer {
    padding-bottom: 80px;
    &__wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        position: relative; }
    &__copyright {
        font-size: 13px;
        font-weight: 600;
        white-space: nowrap; } }

.preloader {
    background:#fff {}
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    outline: none;
    z-index: 100;
    transition-duration: 0s;
    &__loader {
        display: block;
        outline: none;
        position: relative;
        left: 50%;
        top: 50%;
        width: 150px;
        height: 150px;
        margin: -75px 0 0 -75px;
        border-radius: 50%;
        border: 3px solid transparent;
        border-top-color: $main_yellow;
        animation: spin 2s linear infinite;
        transition-duration: 0s;
        &::before {
            content: "";
            position: absolute;
            top: 5px;
            left: 5px;
            right: 5px;
            bottom: 5px;
            border-radius: 50%;
            border: 3px solid transparent;
            border-top-color: $main_yellow;
            animation: spin 3s linear infinite;
            transition-duration: 0s; }
        &::after {
            content: "";
            position: absolute;
            top: 15px;
            left: 15px;
            right: 15px;
            bottom: 15px;
            border-radius: 50%;
            border: 3px solid transparent;
            border-top-color: $main_yellow;
            animation: spin 1.5s linear infinite;
            transition-duration: 0s; } } }

.loaded {
    &_hiding .preloader {
        transition-duration: .3s;
        opacity: 0;
        visibility: hidden; }
    .preloader {
        display: none; } }

@keyframes spin {
    0% {
        transform: rotate(0deg);
        transform: rotate(0deg); }

    100% {
        transform: rotate(360deg);
        transform: rotate(360deg); } }


//
//
// - Страница игр
//
//


.games-page {
    padding-bottom: 65px;
    &__container {
        margin-top: 10px;
        display: grid;
        grid-gap: 40px;
        grid-template-rows: 430px auto;
        grid-template-columns: repeat(3, 1fr); } }

.game {
    display: block;
    position: relative;
    &__img {
        width: 100%;
        height: 260px;
        display: block;
        border-radius: 12px;
        overflow: hidden;
        position: relative;
        z-index: 3;
        img {
            z-index: 1;
            width: 100%;
            height: 100%;
            object-fit: cover;
            transition-duration: .5s;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%); } }
    &:first-of-type {
        grid-column: 1 / 3;
        .game {
            &__img {
                height: 100%;
                max-height: 430px;
                img {
                    box-shadow: inset 0 -40px 100px 10px rgba(0, 0, 0, 0.4); } }
            &__body {
                background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000c4 100%);
                overflow: hidden;
                border-radius: 12px;
                bottom: 0;
                left: 0;
                padding-bottom: 34px;
                padding-left: 34px;
                position: absolute;
                width: 100%; }
            &__name {
                color: #fff;
                font-weight: 800;
                font-size: 44px;
                line-height: 70px;
                margin-bottom: 0;
                max-width: 460px; }
            &__description {
                color: #fff;
                font-size: 20px;
                line-height: 32px;
                font-weight: 500;
                max-width: 460px; } } }
    &__body {
        margin-top: 20px; }
    &__name {
        font-size: 30px;
        font-weight: 400;
        line-height: 48px;
        margin-bottom: 5px; }
    &__description {
        font-size: 16px;
        line-height: 25px;
        font-weight: 400; }
    &:hover {
        .game {
            &__name {
                color: $main_yellow; }
            &__img img {
                transform: translate(-50%, -50%) scale(1.03); } } } }


//
//
// - Страница новостей
//
//


.news-page {
    padding-bottom: 80px;
    &__container {
        overflow: hidden;
        margin-top: 10px;
        margin-bottom: 30px;
        display: grid;
        grid-gap: 92px 36px;
        grid-template-columns: repeat(3, 1fr);
        &.active {
            .news__item:nth-child(n+10) {
                display: block; } }
        .news__item:nth-child(n+10) {
            display: none; } } }


//
//
// - Страница о нас
//
//


.about-page {
    padding-bottom: 80px;
    &__bottom {
        display: flex;
        margin-top: 80px;
        width: 100%; }
    &__text {
        font-weight: 400;
        &_big {
            font-size: 30px;
            line-height: 42px; }
        &_medium + &_small {
            margin-top: 30px; }
        &_medium {
            font-size: 20px;
            line-height: 32px;
            margin-top: 15px; }
        &_small {
            font-size: 16px;
            line-height: 25px;
            & + & {
                margin-top: 1em; } } }
    &__img {
        width: 50%;
        border-radius: 8px;
        overflow: hidden;
        flex-shrink: 0;
        &_float {
            float: right;
            margin-left: 70px;
            margin-bottom: 10px; }
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    &__partnership {
        margin: 10px 40px 0px 65px; }
    &__list {
        margin-top: 25px; }
    &__item {
        color: $main_yellow;
        list-style-position: inside; } }


//
//
// - Страница новости
//
//


.news-item {
    padding-bottom: 80px;
    &__top {
        max-width: 860px; }
    &__date {
        font-weight: 700;
        font-size: 12px;
        line-height: 19px; }
    &__subtitle {
        font-size: 20px;
        line-height: 32px;
        font-weight: 400;
        margin-top: 15px; }
    &__body {
        margin-top: 35px;
        display: grid;
        grid-template-columns: 1fr 360px;
        grid-gap: 55px 60px; }
    &__news {
        display: grid;
        grid-template-columns: 1fr;
        grid-gap: 34px; }
    &__img {
        margin-bottom: 40px;
        width: 100%;
        overflow: hidden;
        border-radius: 12px;
        display: block;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    &__text {
        font-size: 16px;
        font-weight: 400;
        line-height: 25px;
        & + & {
            margin-top: 30px; } } }


//
//
// - Страница игры
//
//


.game-page {
    &__top {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 60px;
        margin-top: 30px; }
    &__content {
        margin: 40px 0px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 60px; }
    &__title {
        font-size: 30px;
        line-height: 48px;
        font-weight: 400;
        margin-bottom: 8px; }
    &__small-text + &__title {
        margin-top: 30px; }
    &__small-text {
        font-size: 16px;
        line-height: 26px; }
    &__picset {
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        grid-template-rows: 400px 95px;
        grid-gap: 24px 20px; }
    &__img {
        grid-column: 1 / 5;
        width: 100%;
        height: 100%;
        max-height: 400px;
        display: block;
        overflow: hidden;
        border-radius: 12px;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover; } }
    &__preview {
        overflow: hidden;
        position: relative;
        z-index: 2;
        padding: 0;
        overflow: hidden;
        img {
            position: relative;
            z-index: 1;
            transition-duration: .4s;
            height: 100%;
            width: 100%;
            object-fit: cover; }
        &:hover, &:active {
            overflow: hidden;
            overflow: hidden;
            img {
                transform: scale(1.03); } } }
    &__description {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        height: 100%;
        width: 100%;
        &-text {
            font-size: 24px;
            line-height: 40px;
            font-weight: 400; }
        .button {
            margin-top: auto; } }
    &__list {
        list-style-position: inside;
        margin-left: 2px; }
    &__item {
        padding: 12px 0px;
        color: $main_yellow;
        &:not(:last-child) {
            border-bottom: 1px solid #E6E6E6; } }
    &__key + &__value {
        margin-left: 3px; }
    &__key {
        font-weight: 500;
        line-height: 35px;
        font-size: 22px; }
    &__value {
        font-weight: 600;
        line-height: 35px;
        font-size: 22px; }
    &__games {
        padding-bottom: 80px; } }


//
//
// - Страница контактов
//
//


.contacts {
    padding-top: 140px;
    padding-bottom: 110px;
    &__container {
        max-width: 550px; }
    &__subtitle {
        font-size: 20px;
        line-height: 32px;
        font-weight: 400;
        margin-top: 16px;
        margin-bottom: 40px; }
    &__form {}
    &__label {
        display: block;
        & + & {
            margin-top: 20px; }
        &:last-child {
            margin-bottom: 40px; } }
    &__name {
        display: block;
        font-size: 16px;
        font-weight: 700;
        line-height: 22px;
        margin-bottom: 8px; }
    &__input {
        display: block;
        border: 1px solid #CFCFCF;
        font-size: 18px;
        line-height: 25px;
        padding: 17px 20px;
        width: 100%;
        &_textarea {
            resize: none;
            min-height: 140px; }
        &:hover {
            border-color: $main_yellow; }
        &:focus {
            border-color: $main_black;
            background-color: #F6F6F6;
            outline: none; }
        &.error {
            background-color: #fff !important;
            border-color: #CFCFCF; } }
    &__bg {
        position: absolute;
        right: 0;
        bottom: 0;
        top: 0;
        width: 50%;
        overflow: hidden;
        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            max-width: 100%; } } }


//
//
// - Попапы
//
//


.popup {
    background-color: #fff;
    width: 100%;
    box-shadow: 0px 28px 52px rgba(0, 0, 0, 0.19);
    max-height: calc( 100% );
    border-radius: 12px;
    max-width: 990px;
    overflow: hidden;
    overflow-y: auto;
    position: absolute;
    transform: translateY(-50px);
    opacity: 0;
    visibility: hidden;
    z-index: 3;
    pointer-events: none;
    &.active {
        position: relative;
        transform: translateY(0px);
        opacity: 1;
        visibility: visible;
        // visibility: hidden
        pointer-events: all; }
    &__form {
        margin-top: 15px; }
    &_request {
        padding: 32px; }
    &_thx {
        padding: 170px 40px 40px; }
    &__close {
        position: absolute;
        height: 30px;
        width: 30px;
        top: 10px;
        right: 10px;
        z-index: 3;
        &::before, &::after {
            content: '';
            display: inline-block;
            width: 24px;
            height: 3px;
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            background-color: $main_yellow; }
        &::before {
            transform: translate(-50%, -50%) rotate(-45deg); }
        &::after {
            transform: translate(-50%, -50%) rotate(45deg); }
        &:active {
            &::before, &::after {
                width: 22px; } }
        &:hover {
            &::before, &::after {
                background-color: $main_black; } } }
    &__title {
        position: relative;
        &_request {
            &::before {
                content: '';
                display: inline-block;
                height: 30px;
                width: 30px;
                background-image: url(../img/request-ico.svg);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                transform: translateY(2px);
                margin-right: 10px; } }
        &_thx {
            margin-bottom: 125px;
            &::before {
                content: '';
                display: inline-block;
                height: 42px;
                width: 100px;
                position: absolute;
                top: -10px;
                left: 50%;
                background-image: url(../img/stars.svg);
                background-repeat: no-repeat;
                background-size: contain;
                background-position: center;
                transform: translate(-50%, -100%);
                margin-right: 10px; } } }
    &__overlay {
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(255, 255, 255, 0.9);
        padding: 0px 20px;
        z-index: -2;
        opacity: 0;
        pointer-events: none;
        visibility: hidden;
        transition-duration: 0s;
        &.active {
            transition-duration: .3s;
            opacity: 1;
            pointer-events: all;
            visibility: visible;
            z-index: 30; } }
    &__col {
        & + & {
            margin-left: 32px; }
        flex: 0 0 48%;
        .contacts__input_textarea {
            min-height: 172px; } }
    &__row {
        display: flex;
        align-items: stretch;
        justify-content: center;
        & + & {
            margin-top: 35px; }
        &_vert {
            flex-direction: column;
            align-items: center; }
        .button {
            margin-top: 0; } }
    .contacts__label {
        margin-bottom: 0; } }
